// src/components/GenderComparisonChart.js
import React, { useEffect, useState } from 'react';
import { Bar } from 'react-chartjs-2';
import axios from 'axios';

const GenderComparisonChart = () => {
  const [chartData, setChartData] = useState(null);

  useEffect(() => {
    axios.get('/api/individuals')
      .then((res) => {
        const data = res.data;
        let genderCounts = { female: 0, male: 0 };
        data.forEach(candidate => {
          const gender = candidate.gender?.toLowerCase().trim();
          if (gender in genderCounts) {
            genderCounts[gender] += 1;
          }
        });
        setChartData({
          labels: ['female', 'male'],
          datasets: [{
            label: 'Candidate Gender Distribution',
            data: [genderCounts.female, genderCounts.male],
            backgroundColor: ['#f06292', '#64b5f6'],
            borderWidth: 1,
          }],
        });
      })
      .catch((err) => console.error(err));
  }, []);

  return (
    <div style={{ height: '300px', marginBottom: '2rem' }}>
      {chartData?.datasets ? (
        <Bar data={chartData} options={{ responsive: true, maintainAspectRatio: false }} />
      ) : (
        <p style={{ textAlign: 'center', fontFamily: 'Roboto Mono, monospace' }}>Loading chart...</p>
      )}
    </div>
  );
};

export default GenderComparisonChart;