// src/components/StickFigureGrid.js
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Grid, Tooltip, Box, Typography } from '@mui/material';
import FemaleIcon from '@mui/icons-material/Female';
import MaleIcon from '@mui/icons-material/Male';

const StickFigureGrid = () => {
  const [candidates, setCandidates] = useState([]);

  useEffect(() => {
    axios.get('/api/individuals')
      .then((res) => {
        setCandidates(res.data || []);
      })
      .catch((err) => console.error(err));
  }, []);

  const renderStickFigure = (candidate) => {
    const isFemale = candidate.gender?.toLowerCase() === 'female';
    const bias = Array.isArray(candidate.bias_flags)
      ? candidate.bias_flags.join(', ').toLowerCase()
      : 'none';

    return (
      <Box key={candidate.id || candidate.name} sx={{ textAlign: 'center', margin: 1 }}>
        <Tooltip 
          title={
            <>
              <Typography variant="body2">name: {candidate.name?.toLowerCase()}</Typography>
              <Typography variant="body2">origin: {candidate.origin?.toLowerCase()}</Typography>
              <Typography variant="body2">education: {candidate.education?.toLowerCase()}</Typography>
              <Typography variant="body2">exp: {candidate.years_experience} yrs</Typography>
              <Typography variant="body2">score: {candidate.qualification_score}</Typography>
              <Typography variant="body2">bias: {bias}</Typography>
            </>
          }
          arrow
        >
          <Box sx={{ cursor: 'pointer' }}>
            {isFemale ? (
              <FemaleIcon sx={{ fontSize: 40, color: '#f06292' }} />
            ) : (
              <MaleIcon sx={{ fontSize: 40, color: '#64b5f6' }} />
            )}
          </Box>
        </Tooltip>
      </Box>
    );
  };

  return (
    <Grid container justifyContent="center" spacing={2}>
      {Array.isArray(candidates) && candidates.length > 0 ? (
        candidates.map(candidate => renderStickFigure(candidate))
      ) : (
        <Typography align="center" sx={{ mt: 2, fontFamily: "'Roboto Mono', monospace" }}>
          loading stick figures...
        </Typography>
      )}
    </Grid>
  );
};

export default StickFigureGrid;
