// src/components/WelcomePage.js
import React, { useState } from 'react';
import { Box, Typography, Button, Dialog, DialogContent, IconButton } from '@mui/material';
import Typical from 'react-typical';
import { keyframes } from '@emotion/react';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate } from 'react-router-dom';

// Define a simple fade-in keyframe for the overlay text
const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`;

const WelcomePage = () => {
  const navigate = useNavigate();
  const [modalOpen, setModalOpen] = useState(false);

  const typedMessages = [
    "Data from the Moss-Racusin et al. (2012) study show that identical resumes with female names receive, on average, 7% lower starting salary offers than those with male names...",
    "Data from field experiments show that women receive approximately 20% fewer interview callbacks than men for the same qualifications.",
    "In Armenia, only 33% of applicants are women vs. 53% of men...",
    "In Egypt, hiring rates show 36% for women compared to 53% for men...",
    "Data exposes bias, its time to create a fairer future!"
  ];

  return (
    <>
      {/* Full-screen background image */}
      <Box
        sx={{
          position: 'fixed', inset: 0,
          backgroundImage: `url(${process.env.PUBLIC_URL}/assets/background.png)`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          zIndex: -1
        }}
      />

      {/* Overlay content */}
      <Box
        sx={{
          position: 'relative',
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          textAlign: 'center',
          p: 2,
          animation: `${fadeIn} 1s ease-in`,
          color: '#ffffff',
          backdropFilter: 'brightness(0.7)'
        }}
      >
        <Typography
          variant="h2"
          sx={{
            fontFamily: "'Roboto Mono', monospace",
            fontSize: { xs: '2.5rem', md: '3.5rem' },
            mb: 2,
          }}
        >
          welcome to visualizing bias
        </Typography>

        <Typography
          variant="h5"
          sx={{
            mb: 4,
            px: 2,
            fontFamily: "'Roboto Mono', monospace",
          }}
        >
          <Typical
            steps={typedMessages.flatMap(msg => [msg, 3000])}
            loop={Infinity}
            wrapper="span"
          />
        </Typography>

        <Button
          variant="contained"
          size="large"
          sx={{
            backgroundColor: '#ff5722',
            color: '#fff',
            fontWeight: 600,
            borderRadius: '30px',
            px: 4,
            py: 1.5,
            boxShadow: 3,
            fontFamily: "'Roboto Mono', monospace",
            '&:hover': { backgroundColor: '#e64a19' },
            mb: 2,
          }}
          onClick={() => navigate('/login')}
        >
          log in
        </Button>

        <Button
          variant="outlined"
          size="large"
          sx={{
            borderColor: '#fff',
            color: '#fff',
            fontFamily: "'Roboto Mono', monospace",
            px: 4,
            py: 1.5,
            '&:hover': { borderColor: '#ddd' },
          }}
          onClick={() => setModalOpen(true)}
        >
          learn more
        </Button>
      </Box>

      {/* Learn More Modal */}
      <Dialog open={modalOpen} onClose={() => setModalOpen(false)} maxWidth="md" fullWidth>
        <Box sx={{ display: 'flex', justifyContent: 'flex-end', p: 1 }}>
          <IconButton onClick={() => setModalOpen(false)}>
            <CloseIcon />
          </IconButton>
        </Box>
        <DialogContent>
          <Typography variant="h6" sx={{ fontFamily: "'Roboto Mono', monospace", mb: 1 }}>
            understanding bias in hiring
          </Typography>
          <Typography variant="body1" sx={{ fontFamily: "'Roboto Mono', monospace" }}>
            Persistent bias in hiring creates unjust disparitieswomen and migrant women face severe discrimination.
            Our platform transforms data into interactive insights to help address these issues.
          </Typography>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default WelcomePage;
