// src/components/HiringRatioChart.js
import React, { useEffect, useState } from 'react';
import { Bar } from 'react-chartjs-2';
import axios from 'axios';

const HiringRatioChart = () => {
  const [chartData, setChartData] = useState(null);

  useEffect(() => {
    axios.get('/api/individuals')
      .then((res) => {
        const data = res.data;
        const decisions = {
          hired: 0,
          rejected: 0,
          interview: 0,
        };
        data.forEach(candidate => {
          const decision = candidate.ai_decision?.toLowerCase().trim();
          if (decision in decisions) {
            decisions[decision] += 1;
          }
        });
        setChartData({
          labels: ['hired', 'rejected', 'interview'],
          datasets: [
            {
              label: 'Hiring Decisions',
              data: [decisions.hired, decisions.rejected, decisions.interview],
              backgroundColor: ['#4caf50', '#f44336', '#ff9800'],
              borderWidth: 1,
            }
          ]
        });
      })
      .catch((err) => console.error(err));
  }, []);

  return (
    <div style={{ height: '300px', marginBottom: '2rem' }}>
      {chartData?.datasets ? (
        <Bar data={chartData} options={{ responsive: true, maintainAspectRatio: false }} />
      ) : (
        <p style={{ textAlign: 'center', fontFamily: 'Roboto Mono, monospace' }}>Loading chart...</p>
      )}
    </div>
  );
};

export default HiringRatioChart;