// src/components/ApiTester.js
import React, { useState } from 'react'
import { Box, Typography, TextField, Button, Paper, Alert } from '@mui/material'
import axios from 'axios'

/**
 * API playground: POST JSON to any endpoint and view the response.
 */
const ApiTester = () => {
  const [endpoint, setEndpoint] = useState('/api/bias-fixer')
  const [jsonBody, setJsonBody] = useState(`{
  "minScore": 70,
  "tolerance": 0.5
}`)
  const [responseText, setResponseText] = useState('')
  const [errorText, setErrorText] = useState('')

  // Base URL from .env (optional), default to same origin
  const BASE = process.env.REACT_APP_NODE_API_URL || ''

  const handleSend = async () => {
    setErrorText('')
    setResponseText('')
    try {
      const payload = JSON.parse(jsonBody)
      const url = endpoint.startsWith('/')
        ? `${BASE}${endpoint}`
        : endpoint

      const { data } = await axios.post(url, payload, {
        headers: { 'Content-Type': 'application/json' }
      })
      setResponseText(JSON.stringify(data, null, 2))
    } catch (err) {
      console.error('API request failed', err)
      const msg = err.response?.data?.error
        || err.response?.data?.detail
        || err.message
      setErrorText(msg)
    }
  }

  return (
    <Box sx={{ p: 3, minHeight: '100vh', backgroundColor: '#e8f0fe' }}>
      <Typography
        variant="h4"
        align="center"
        gutterBottom
        sx={{
          fontFamily: 'Roboto Mono, monospace',
          textTransform: 'lowercase',
          color: '#2c3e50'
        }}
      >
        api tester
      </Typography>

      <Paper sx={{ maxWidth: 900, mx: 'auto', p: 3, borderRadius: 2, boxShadow: 3 }}>
        <TextField
          label="endpoint"
          value={endpoint}
          onChange={e => setEndpoint(e.target.value)}
          fullWidth
          margin="normal"
          sx={{
            fontFamily: 'Roboto Mono, monospace',
            textTransform: 'lowercase'
          }}
        />

        <TextField
          label="json payload"
          value={jsonBody}
          onChange={e => setJsonBody(e.target.value)}
          multiline
          minRows={8}
          fullWidth
          margin="normal"
          sx={{ fontFamily: 'Roboto Mono, monospace' }}
        />

        <Button
          variant="contained"
          onClick={handleSend}
          sx={{ textTransform: 'lowercase', mt: 2 }}
        >
          send request
        </Button>

        {errorText && (
          <Alert
            severity="error"
            sx={{ mt: 3, whiteSpace: 'pre-wrap', fontFamily: 'Roboto Mono, monospace' }}
          >
            {errorText}
          </Alert>
        )}

        {responseText && (
          <TextField
            label="response"
            value={responseText}
            multiline
            minRows={10}
            fullWidth
            margin="normal"
            InputProps={{ readOnly: true }}
            sx={{
              mt: 3,
              fontFamily: 'Roboto Mono, monospace',
              backgroundColor: '#f4f6f8'
            }}
          />
        )}
      </Paper>
    </Box>
  )
}

export default ApiTester;