// src/components/SimulatedHiringPage.js
import React, { useState, useEffect } from 'react'
import { Box, Typography, TextField, Button, MenuItem, Alert } from '@mui/material'
import axios from 'axios'

const SimulatedHiringPage = () => {
  const [countries, setCountries] = useState([])
  const [formData, setFormData] = useState({
    country: '',
    age_group: 0,
    education_level: 1,
    professional_developer: 1,
    years_code: 3
  })
  const [result, setResult] = useState(null)

  // your backend proxy (for /api/countries and /api/country-stats)
  const NODE = process.env.REACT_APP_NODE_API_URL || ''
  // direct ML endpoint (or you could proxy this through NODE if you add /api/predict)
  const ML   = process.env.REACT_APP_ML_API_URL  || ''

  // 1 Load list of countries
  useEffect(() => {
    axios
      .get(`${NODE}/api/countries`)
      .then(res => setCountries(res.data))
      .catch(err => console.error('Failed to load countries', err))
  }, [NODE])

  const handleChange = field => e => {
    setFormData({ ...formData, [field]: e.target.value })
  }

  const handleSubmit = async () => {
    try {
      // lowercase the country in the URL
      const countryKey = formData.country.toLowerCase()

      // 2 Fetch the 6 countrylevel stats
      const { data: stats } = await axios.get(
        `${NODE}/api/country-stats/${countryKey}`
      )

      // 3 Build the 10field payload, matching your JSON casing exactly
      const payload = {
        age_group:               parseInt(formData.age_group, 10),
        education_level:         parseInt(formData.education_level, 10),
        professional_developer:  parseInt(formData.professional_developer, 10),
        years_code:              parseFloat(formData.years_code),
        pct_female_highered:     stats.Pct_Female_HigherEd,
        pct_male_highered:       stats.Pct_Male_HigherEd,
        pct_female_mided:        stats.Pct_Female_MidEd,
        pct_male_mided:          stats.Pct_Male_MidEd,
        pct_female_lowed:        stats.Pct_Female_LowEd,
        pct_male_lowed:          stats.Pct_Male_LowEd
      }

      // 4 Call your external ML API
      const { data: mlResult } = await axios.post(
        `${ML}/predict`,
        payload
      )

      setResult(mlResult)

    } catch (err) {
      console.error('Simulation error', err)
      setResult({
        error: err.response?.data?.detail
               || err.message
               || 'Something went wrong'
      })
    }
  }

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" align="center" sx={{ textTransform: 'lowercase', mb: 2 }}>
        simulate hiring outcome
      </Typography>
      <Typography variant="body2" align="center" sx={{ mb: 3, color: 'gray' }}>
        Note: we do not collect individual gender hereour model uses aggregate
        countrylevel gender ratios to mitigate bias.
      </Typography>

      <Box sx={{ maxWidth: 500, mx: 'auto' }}>
        <TextField
          select
          label="country"
          value={formData.country}
          onChange={handleChange('country')}
          fullWidth
          margin="normal"
        >
          {countries.map(c => (
            <MenuItem key={c} value={c}>{c}</MenuItem>
          ))}
        </TextField>

        <TextField
          label="age group"
          type="number"
          helperText="0 = <35, 1 = 35"
          value={formData.age_group}
          onChange={handleChange('age_group')}
          fullWidth
          margin="normal"
        />

        <TextField
          label="education level"
          type="number"
          helperText="0 = High School, 1 = Bachelor, 2 = Master, 3 = PhD"
          value={formData.education_level}
          onChange={handleChange('education_level')}
          fullWidth
          margin="normal"
        />

        <TextField
          label="professional developer"
          type="number"
          helperText="0 = no, 1 = yes"
          value={formData.professional_developer}
          onChange={handleChange('professional_developer')}
          fullWidth
          margin="normal"
        />

        <TextField
          label="years coding"
          type="number"
          helperText="Total years of professional coding experience"
          value={formData.years_code}
          onChange={handleChange('years_code')}
          fullWidth
          margin="normal"
        />

        <Button
          variant="contained"
          onClick={handleSubmit}
          sx={{ mt: 2, textTransform: 'lowercase' }}
        >
          run simulation
        </Button>
      </Box>

      {result && (
        <Box sx={{ mt: 4 }}>
          {result.error ? (
            <Alert severity="error">{result.error}</Alert>
          ) : (
            <Alert severity="success">
              Qualification Score: <strong>{result.qualification_score}</strong><br/>
              Decision: <strong>{result.ai_decision}</strong>
            </Alert>
          )}
        </Box>
      )}
    </Box>
  )
}

export default SimulatedHiringPage
