import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import './App.css'; // Optional styling

// Chart.js module registration 
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  ArcElement, //  Required for Doughnut and Pie charts
  Tooltip,
  Legend,
  Title
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  ArcElement, //  This fixes the "arc is not a registered element" error
  Tooltip,
  Legend,
  Title
);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<App />);
