// src/components/BiasFixerPlayground.js
import React, { useEffect, useState } from 'react'
import { Box, Typography, Slider, Paper, Alert } from '@mui/material'
import axios from 'axios'
import { Bar } from 'react-chartjs-2'
import WordCloudView from './WordCloudView'

const BiasFixerPlayground = () => {
  const [minScore, setMinScore]   = useState(70)
  const [tolerance, setTolerance] = useState(0.5)
  const [result, setResult]       = useState([])

  // Base URL of your Node proxy from .env (e.g. "https://your-backend.com")
  const NODE = process.env.REACT_APP_NODE_API_URL || ''

  // Run bias-fixer simulation via Node each time sliders change
  useEffect(() => {
    const runSimulation = async () => {
      try {
        const { data } = await axios.post(
          `${NODE}/api/bias-fixer`,
          { minScore, tolerance }
        )
        setResult(data)
      } catch (err) {
        console.error('Simulation error:', err)
        setResult([])
      }
    }

    runSimulation()
  }, [NODE, minScore, tolerance])

  // Compute hires counts and uplift %
  const originalHires = result.filter(r => r.original_score >= minScore).length
  const adjustedHires = result.filter(r => r.hired).length
  const uplift = originalHires > 0
    ? Math.round(((adjustedHires - originalHires) / originalHires) * 100)
    : (adjustedHires > 0 ? 100 : 0)

  // Prepare Chart.js data
  const chartData = {
    labels: ['original', 'adjusted'],
    datasets: [{
      label: 'hired candidates',
      data: [originalHires, adjustedHires]
    }]
  }

  return (
    <Box sx={{ maxWidth: 700, mx: 'auto', mt: 5 }}>
      <Typography variant="h5" align="center" sx={{ mb: 3, textTransform: 'lowercase' }}>
        bias fixer playground
      </Typography>
      <Typography variant="body2" align="center" sx={{ mb: 2, color: 'gray' }}>
        Adjust the minimum qualification score and bias tolerance.<br/>
        Our ML model evaluates each candidate and then applies bias adjustments based on flagged attributes.
      </Typography>

      <Paper sx={{ p: 3, backgroundColor: '#f4f6f8' }}>
        <Typography sx={{ textTransform: 'lowercase' }}>
          minimum score: {minScore}
        </Typography>
        <Slider
          value={minScore}
          onChange={(e, v) => setMinScore(v)}
          min={0} max={100} step={5}
        />

        <Typography sx={{ mt: 2, textTransform: 'lowercase' }}>
          bias tolerance: {tolerance.toFixed(2)}
        </Typography>
        <Slider
          value={tolerance}
          onChange={(e, v) => setTolerance(v)}
          min={0} max={1} step={0.01}
        />

        <Box sx={{ mt: 4, height: 300 }}>
          <Bar data={chartData} options={{ responsive: true, maintainAspectRatio: false }} />
        </Box>

        <Typography align="center" sx={{ mt: 2, fontWeight: 600, textTransform: 'lowercase' }}>
          fairness impact score: {uplift >= 0 ? `+${uplift}%` : `${uplift}%`}
        </Typography>
        <Typography variant="caption" align="center" sx={{ display: 'block', mb: 2, textTransform: 'lowercase' }}>
          ({originalHires}  {adjustedHires} hired with bias mitigation)
        </Typography>

        {result.slice(0, 10).map((r, i) => (
          <Alert key={i} severity={r.hired ? 'success' : 'warning'} sx={{ mb: 1 }}>
            {r.name}: original {r.original_score.toFixed(2)}, adjusted {r.adjusted_score.toFixed(2)}  {r.hired ? ' hired' : ' rejected'}
          </Alert>
        ))}
      </Paper>

      <WordCloudView />
    </Box>
  )
}

export default BiasFixerPlayground
