// src/components/BiasDistributionChart.js
import React, { useEffect, useState } from 'react';
import { Doughnut } from 'react-chartjs-2';
import axios from 'axios';

const BiasDistributionChart = () => {
  const [chartData, setChartData] = useState(null);

  useEffect(() => {
    axios.get('/api/individuals')
      .then((res) => {
        const data = res.data;
        const biasCounts = {};

        data.forEach(candidate => {
          const flags = candidate.bias_flags || [];
          flags.forEach(flag => {
            const normalizedFlag = flag.toLowerCase();
            biasCounts[normalizedFlag] = (biasCounts[normalizedFlag] || 0) + 1;
          });
        });

        const labels = Object.keys(biasCounts);
        const counts = Object.values(biasCounts);

        const backgroundColors = labels.map(label => {
          if (label.includes('gender')) return '#f44336';
          if (label.includes('migrant')) return '#ff9800';
          if (label.includes('non-degree')) return '#3f51b5';
          return '#4caf50';
        });

        setChartData({
          labels: labels,
          datasets: [{
            data: counts,
            backgroundColor: backgroundColors,
            hoverBackgroundColor: backgroundColors,
          }],
        });
      })
      .catch((err) => console.error(err));
  }, []);

  return (
    <div style={{ height: '300px', marginBottom: '2rem' }}>
      {chartData?.datasets ? (
        <Doughnut data={chartData} options={{ responsive: true, maintainAspectRatio: false }} />
      ) : (
        <p style={{ textAlign: 'center', fontFamily: 'Roboto Mono, monospace' }}>Loading chart...</p>
      )}
    </div>
  );
};

export default BiasDistributionChart;
