// src/components/AdminDashboard.js
import React, { useEffect, useState } from 'react';
import { Box, Typography, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, Paper, Button } from '@mui/material';
import axios from 'axios';
import { Bar } from 'react-chartjs-2';

const AdminDashboard = () => {
  const [logs, setLogs] = useState([]);
  const [chartData, setChartData] = useState({});

  useEffect(() => {
    axios.get('/api/logs')
      .then((res) => {
        const data = res.data;
        setLogs(data);

        const aggregated = {};
        data.forEach(log => {
          const time = new Date(log.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
          aggregated[time] = (aggregated[time] || 0) + 1;
        });
        const times = Object.keys(aggregated);
        const counts = Object.values(aggregated);

        setChartData({
          labels: times,
          datasets: [
            {
              label: 'login frequency',
              data: counts,
              backgroundColor: '#3f51b5',
              borderWidth: 1,
            }
          ],
        });
      })
      .catch((err) => console.error(err));
  }, []);

  return (
    <Box sx={{ p: 3 }}>
      <Typography 
        variant="h4" 
        align="center" 
        sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase', mb: 4 }}
      >
        admin dashboard
      </Typography>

      <Typography 
        variant="h6" 
        align="center" 
        sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase', mb: 2 }}
      >
        login frequency chart
      </Typography>
      <Box sx={{ height: '300px', mb: 4 }}>
        {chartData?.datasets?.length > 0 ? (
          <Bar data={chartData} options={{ responsive: true, maintainAspectRatio: false }} />
        ) : (
          <Typography align="center" sx={{ mt: 4, fontFamily: "'Roboto Mono', monospace" }}>
            loading chart...
          </Typography>
        )}
      </Box>

      <Typography 
        variant="h6" 
        align="center" 
        sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase', mb: 2 }}
      >
        login logs
      </Typography>
      <TableContainer component={Paper} sx={{ mb: 4 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }}>username</TableCell>
              <TableCell sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }}>timestamp</TableCell>
              <TableCell sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }}>ip address</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {Array.isArray(logs) && logs.length > 0 ? (
              logs.map((log, index) => (
                <TableRow key={index}>
                  <TableCell sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }}>{log.username}</TableCell>
                  <TableCell sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }}>
                    {new Date(log.timestamp).toLocaleString()}
                  </TableCell>
                  <TableCell sx={{ fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }}>{log.ip}</TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={3} align="center">
                  no login records yet
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>

      <Box textAlign="center">
        <Button 
          variant="contained" 
          color="secondary" 
          sx={{ 
            fontFamily: "'Roboto Mono', monospace", 
            textTransform: 'lowercase', 
            px: 4, 
            py: 1 
          }}
          href="/api/export"
        >
          export data as csv
        </Button>
      </Box>
    </Box>
  );
};

export default AdminDashboard;