import React, { useEffect, useState } from 'react';
import { Box, Typography } from '@mui/material';
import axios from 'axios';

const WordCloudView = () => {
  const [biasData, setBiasData] = useState([]);

  useEffect(() => {
    axios.get('/api/summary').then((res) => {
      const raw = res.data.biasDistribution;
      const sorted = Object.entries(raw).sort((a, b) => b[1] - a[1]);
      setBiasData(sorted);
    });
  }, []);

  return (
    <Box sx={{ mt: 4, textAlign: 'center' }}>
      <Typography variant="h6" sx={{ textTransform: 'lowercase', mb: 2 }}>
        bias word cloud
      </Typography>
      <Box sx={{ display: 'flex', gap: 1, justifyContent: 'center', flexWrap: 'wrap' }}>
        {biasData.map(([word, size], idx) => (
          <Typography
            key={idx}
            sx={{
              fontSize: `${Math.min(30, 12 + size * 2)}px`,
              textTransform: 'lowercase',
              color: '#3f51b5',
              fontWeight: 600
            }}
          >
            {word}
          </Typography>
        ))}
      </Box>
    </Box>
  );
};

export default WordCloudView;
